import { createAsyncThunk, createSlice } from '@reduxjs/toolkit';
import axios from 'axios';

import { getUserSlug } from '../../../../utils';
import { fetchLists } from '../lists/lists.slice';

const fetchList = createAsyncThunk('users/list/fetch', async function (
  { id, userSlug = getUserSlug() },
  { rejectWithValue },
) {
  try {
    const [listResponse, itemsResponse] = await Promise.all([
      axios.get(`/trakt/users/${userSlug}/lists/${id}`),
      axios.get(`/trakt/users/${userSlug}/lists/${id}/items`),
    ]);
    return {
      ...listResponse.data,
      items: itemsResponse.data,
    };
  } catch (error) {
    return rejectWithValue(error.toString());
  }
});

const removeFromList = createAsyncThunk('users/list/remove', async function (
  { entity, listSlug, slug },
  { rejectWithValue },
) {
  try {
    await axios.post(
      `/trakt/users/${getUserSlug()}/lists/${listSlug}/items/remove`,
      {
        [entity]: [{ ids: { slug } }],
      },
    );
  } catch (error) {
    return rejectWithValue(error.toString());
  }
});

const addToList = createAsyncThunk('users/list/add', async function (
  { entity, listSlug, slug },
  { rejectWithValue },
) {
  try {
    await axios.post(`/trakt/users/${getUserSlug()}/lists/${listSlug}/items`, {
      [entity]: [{ ids: { slug } }],
    });
  } catch (error) {
    return rejectWithValue(error.toString());
  }
});

function addToListAndRefetch(params) {
  return async function (dispatch) {
    await dispatch(addToList(params));
    await dispatch(fetchLists());
  };
}

function removeFromListAndRefetch(params) {
  return async function (dispatch) {
    await dispatch(removeFromList(params));
    await dispatch(fetchLists());
  };
}

const { reducer } = createSlice({
  extraReducers: {
    [fetchList.fulfilled](state, action) {
      return action.payload;
    },
  },
  initialState: {
    items: [],
  },
  name: 'list',
  reducers: {},
});

export { addToListAndRefetch, fetchList, removeFromListAndRefetch };
export default reducer;
